@use 'variables' as v;

@mixin phone {
    @media (min-width: v.$telefono){
        @content;
    }
}

@mixin tablet {
    @media (min-width: v.$tablet){
        @content;
    }
}

@mixin desktop {
    @media (min-width: v.$desktop){
        @content;
    }
}

@mixin desktopXL {
    @media (min-width: v.$desktopXL){
        @content;

    }
}

@mixin grid ($columns, $espaciado) {
    display: grid;
    grid-template-columns: repeat($columns, 1fr);
    gap: $espaciado;
}

@mixin resetear-Lista{
    margin: 0;
    padding: 0;
    list-style: none;
}





